/** @jsx jsx */
import type { HeadFC, PageProps } from "gatsby"
import { jsx, Heading } from "theme-ui"
import Layout from "./layout"
import Seo from "./seo"

import CryptoJS from 'crypto-js'
import React, { useState } from "react"
import ReactDOMServer from "react-dom/server";


export type MBPageProps = {
  page: {
    title: string
    slug: string
    excerpt: string
  }
}

const Encrypted = ({ html }) => {
  // the decrypted content
  const [decrypted, setDecrypted] = useState('')
  // save user input
  const [password, setPassword] = useState('')

  return decrypted ? (
    <section
      dangerouslySetInnerHTML={{ __html: decrypted }}
      itemProp="articleBody"
    />
  ) : (
    <section itemProp="articleBody">
      <input
        type='password'
        value={password}
        onChange={e => setPassword(e.target.value)}
        onKeyUp={e => {
          if (e.keyCode === 13) {
            try {
              setDecrypted(CryptoJS.AES.decrypt(html, password).toString(CryptoJS.enc.Utf8))
            } catch (e) {
              alert('password error')
            }
          }
        }}
      />
    </section>
  )
}

const Page: React.FC<React.PropsWithChildren<PageProps<MBPageProps>>> = ({ data: { page }, children }) => {
  // FIXME: add encrypted field to children.props.data.page.xxx(slug, title, excerpt)
  // console.log(children)
  const { frontmatter } = children.props.pageContext;
  let decrypted
  if (frontmatter && frontmatter.access && frontmatter.access.trim() !== "") {
    // console.log(frontmatter.access)
    const componentAsString = ReactDOMServer.renderToString(children).replace(/<span[^>]*>|<\/span>/g, "")
    // decrypt should happen after page loaded
    // const decrypted_text = CryptoJS.AES.decrypt(componentAsString, frontmatter.access).toString(CryptoJS.enc.Utf8)
    // decrypted = (<div dangerouslySetInnerHTML={{ __html: decrypted_text }} />)
    decrypted = (<Encrypted html={componentAsString}/>)
  }

  return (
    <Layout>
      <Heading as="h1" variant="styles.h1">
        {page.title}
      </Heading>
      <section sx={{ my: 5, variant: `layout.content` }}>
        {decrypted ? decrypted : children}
      </section>
    </Layout>
  )
}

export default Page

export const Head: HeadFC<MBPageProps> = ({ data: { page } }) => <Seo title={page.title} description={page.excerpt} />
